.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
  padding: 20px;
  border: 1px solid #000000;
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.avatar {
  width: 120px;
  border-radius: 50%;
  border: 2px solid #000000;
}

.name {
  font-size: 26px;
  font-weight: 700;
}

.tag {
  font-size: 18px;
  font-weight: 500;
  color: #999;
}

.location {
  font-size: 16px;
  font-weight: 500;
  color: #000000;
  margin-bottom: 20px;
}

.stats {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 20px;
  list-style-type: none;
  background-color: #f3f6f9;
}

.label {
  font-size: 16px;
  font-weight: 500;
  color: #000000;
}

.quantity {
  font-size: 18px;
  font-weight: 700;
  color: #000000;
}
