.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 20px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.title {
  margin-bottom: 20px;
  font-size: 20px;
  color: #303f9f;
}

.statlist {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100px;
  padding: 10px;
  border-radius: 4px;
  background-color: #f3f6f9;
}

.label {
  margin-bottom: 5px;
  font-size: 12px;
  color: #757575;
}

.percentage {
  font-size: 18px;
  font-weight: 500;
  color: #303f9f;
}
